.userPhoto {
    width: 60px;
    height: 60px;
    border-radius: 50px;
    margin: 0 20px;
}

.usersBlock {
    display: flex;
    align-items: center;
    flex-flow: row nowrap;
}

.selectedPage {
    font-weight: bold;
}

.paginator {
    padding: 10px;
    margin-bottom: 50px;
}

.pageNumber {
    padding: 5px 15px;
    border-radius: 3px;
    background-color: #5692d7;
    color: #fff;
    margin: 5px;
}

.pageNumber.selectedPage {
    font-weight: bold;
    background-color: #407bbd;
}


.headerModule {
    margin-top: 30px;
    margin-bottom: 20px;
    margin-left: 12px;
}

.usersInf {
    margin-top: 5px;
    float: right;
}

.hr {
    border: none;
    height: 2px;
    background-color: #f0f0f0;
}

.buttonPrevNext {

    padding: 5px 10px;
    border-radius: 3px;
    background-color: #5692d7;
    color: #fff;
    margin: 5px;
    border: none;
}

.foluflo {
    padding: 7px;
    background-color: #5692d7;
    border-radius: 3px;
    border: none;
    color: #fff;
    margin-top: 100px;
    float: left;
}