.header img {
  height: 40px;
  margin: 5px 0 5px 300px;
}

.header {
  background-color: #5692d7;
  height: 50px;
  display: block;
  position: fixed;
  width: 100%;
}

.loginBlock {
  float: right;
  margin-right: 320px;
}

.loginBlock a {
  color: aliceblue;
}

.logo {

}

.login {
  color: #fff;
  margin: 0 10px;
}

.wrapper {
  display: flex;
  align-items: center;
  height: 50px;
  flex-flow: row nowrap;
}

.buttons {
  height: 30px;
  background-color: #fff;
  border: none;
  border-radius: 3px;
  color: #000;
  width: 100px;
  transition: .3s all;
}

.buttons:hover {
  background-color: #f0f0f0;
  cursor: pointer;
}

.buttons:active {
  background-color: cornflowerblue;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}
