.nav {
    font-size: 15px;
    background-color: #faf9f5;
    padding: 20px 0;
    width: 200px;
    height: calc(100% - 50px);
    margin-left: 290px;
    margin-top: 20px;
}

.item a {
    color: black;
    text-decoration: none;
    padding: 12px;
    font-size: 17px;
    display: block;
}

.item a:hover {
    background-color: #f5f5f5;
}

.item a.active {
    color: blue;
}

.logo {
    height: 17px;
    width: 17px;
    margin: 0 10px;
}
.linksModule{
    background-color: #faf9f5;
    margin-top: 30px;
}